import React from "react";
import Square from "../Square/index.js";
import Grid from '@material-ui/core/Grid';
import IconButton from '@material-ui/core/IconButton';
import PlayIcon from '@material-ui/icons/PlayCircleFilled';
import PauseIcon from '@material-ui/icons/PauseCircleFilled';
import NextIcon from '@material-ui/icons/SkipNext';
import DeleteIcon from '@material-ui/icons/Delete';
import SpeedSlider from "../SpeedSlider";
import "./index.css";

export default class Game extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      map: Array(32).fill().map(() => Array(32).fill((0,0))),
      height: 32,
      width: 32,
      speed: 1,
      playing: false
    };

    this.alive = [];
    this.interval = null;

    this.next = this.next.bind(this);
    this.onClick = this.onClick.bind(this);
    this.onSlide = this.onSlide.bind(this);
    this.play = this.play.bind(this);
    this.stop = this.stop.bind(this);
    this.clear = this.clear.bind(this);
  }

  next() {
    var nextState = Array(34).fill().map(() => Array(34).fill().map(() => Array(2).fill(0)));

    for (let i = 0; i < this.alive.length; i++) {
      let j = this.alive[i][0] + 1;
      let k = this.alive[i][1] + 1;

      nextState[j][k][0] += 1;
      nextState[j-1][k][1] += 1;
      nextState[j-1][k-1][1] += 1;
      nextState[j-1][k+1][1] += 1;
      nextState[j][k-1][1] += 1;
      nextState[j][k+1][1] += 1;
      nextState[j+1][k][1] += 1;
      nextState[j+1][k-1][1] += 1;
      nextState[j+1][k+1][1] += 1;
    }

    this.alive = [];
    let nextMap = this.state.map;
    
    for (let i = 1; i < this.state.height + 1; i++) {
      for (let j = 1; j < this.state.width + 1; j++) {
        let current = nextState[i][j];
        if (current[0]) {
          if (current[1] === 2 || current[1] === 3) {
            nextMap[i - 1][j - 1] = 1;
            this.alive.push([i - 1, j - 1])
            continue;
          }
          else {
            nextMap[i - 1][j - 1] = 2;
            continue;
          }
        }
        else {
          if (current[1] === 3) {
            nextMap[i - 1][j - 1] = 1;
            this.alive.push([i - 1 , j - 1]);
            continue;
          }
        }

        nextMap[i - 1][j - 1] = 0;
      }
    }

    this.setState({map: nextMap})
  }

  onClick(row, col) {
    let map = this.state.map;
    let index = -1;

    for (let i = 0; i < this.alive.length; i++) {
      if (this.alive[i][0] === row && this.alive[i][1] === col) {
        index = i;
        break;
      }
    }

    if (index > -1) {
      this.alive.splice(index, 1);
      map[row][col] = 0;
    }
    else {
      this.alive.push([row, col])
      map[row][col] = 1;
    }

    this.setState({map: map})
  }

  onSlide(event, newValue) {
    this.setState({speed: newValue}, () => { if (this.state.playing) this.play()});
  }

  play() {
    if (this.interval != null) {
      clearInterval(this.interval);
    }

    this.interval = setInterval(this.next, 1000 / this.state.speed);
    this.setState({playing: true});
  }


  stop() {
    if (this.interval != null) {
      clearInterval(this.interval);
      this.setState({playing: false});
    }
  }

  clear() {
    this.alive = [];
    this.setState({map: Array(32).fill().map(() => Array(32).fill((0,0)))})
  }

  render() {
    var map = []
    console.log(this.props.theme)
    for (let i = 0; i < this.state.height; i++) {
      for (let j = 0; j < this.state.width; j++) {
        map.push(<Square value={this.state.map[i][j]} colour={this.props.theme} row={i} col={j} onClick={this.onClick}/>);
      }
    }

    return (
      <div className="game">
        <div className="map">
          {map}
        </div>
        <Grid className="controls" container spacing={1} alignItems="center">
          <IconButton color={"#C4C4C4"} xs={1} onClick={this.state.playing ? this.stop : this.play}>
            {this.state.playing ? <PauseIcon style={{ color: this.props.theme, fontSize: 60 }}/> :
              <PlayIcon style={{ color: this.props.theme, fontSize: 60 }}/>}
          </IconButton>
          <IconButton color={"#C4C4C4"} xs={1} onClick={this.next}><NextIcon fontSize="large"/></IconButton>
          <IconButton color={"#C4C4C4"} xs={1} onClick={this.clear}><DeleteIcon fontSize="large"/></IconButton>
          <Grid container className="play-slider" xs={8} spacing={2} alignItems="center" justify="flex-end">
            <Grid item className="title" xs={4}>Speed</Grid>
            <Grid item className="slider" xs={6}><SpeedSlider onChangeCommitted={this.onSlide} colour={this.props.theme}
              aria-labelledby="continuous-slider" min={1} max={50} step={1}/></Grid>
          </Grid>
        </Grid>
      </div>
    );
  }
}
